.responsively-lazy:not(img) {
	position: relative;
	height: 0
}

.responsively-lazy:not(img) > img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%
}

img.responsively-lazy {
	width: 100%;
}
